import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { showModalBatalBeli } from "../../../actions/pembelian_action.jsx";
import { formatDate, localStoragedecryp, localStorageencryp } from "../../../components/helpers/function.jsx";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import { Panel, PanelBody, PanelHeader } from "../../../components/panel/panel.jsx";
import { postData } from "../../../config/axios.jsx";
import HeadBatalBeli from "./HeadBatalBeli.jsx";
import HeadPembelian from "./HeadPembelian.jsx";
import * as moment from "moment-timezone";
import Loading from 'react-fullscreen-loading';

// import HeadPembelian from "./HeadPembelian.jsx";
class FormLihatPembelian extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoadingServicesSelesai: false,
      isLoadingBatalBeli: false,
      isLoading: false,
      dataservices: [],
      FormModal: false,
      pembayaran: false,
      columns: [
       
        {
          dataField: "no_faktur_beli",
          text: "No FB",
          footerAttrs: {
            colSpan: "3",
          },
          footer: "Total",
        },
        {
          dataField: "nama_customer",
          text: "Nama Customer",
        },
        // {
        //   dataField: "alamat_customer",
        //   text: "Alamat Customer",
        // },
        {
          dataField: "no_hp",
          text: "No Hp",
        },
        {
          dataField: "harga",
          text: "Harga",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "harga_nota",
          text: "Harga Nota",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "harga_rata",
          text: "Harga Rata",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "kondisi",
          text: "Kondisi",
          footer: "",
        },
        {
          dataField: "ambil",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row, index) => {
            return (
              <div className="row">
                <div className="col-12">
                  <button
                    type="button"
                    data-tooltip="Batal Pembelian"
                    onClick={() => this.shoBatalPembelian(row)}
                    className="btn btn-danger"
                  >
                    <i className="fa fa-file-export"></i>
                  </button>
                  &nbsp;
                  <button
                    data-tooltip="Reprint Pembelian"
                    type="button"
                    onClick={() => this.ReprintPembelian(row)}
                    className="btn btn-success mr-3"
                  >
                    <i className="fa fa-print"></i>
                  </button>
                </div>
               
              </div>
            );
          },
          footer: "",
        },
        
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  ReprintPembelian(id) {
    let hasil = {
      no_faktur_group :  id.no_faktur_group
     }
postData('pembelian/get/bynota', hasil).then((res) => {
let feedback = '';
res.data.notabeli.forEach((row, index) => {
let jml_alamat = row.head.n_alamat_customer.length;
feedback = `========================================
Tanggal          : ${row.head.tgl_system}
Nama Customer    : ${row.head.nama_customer}
No Hp            : ${row.head.no_hp}
Alamat Customer  : ${row.head.n_alamat_customer.split(0,20)}
`
if(jml_alamat>20){
feedback += `                   ${row.head.n_alamat_customer.split(0,20)}
`;
}
if(jml_alamat>40){
feedback += `                   ${row.head.n_alamat_customer.split(40,20)}
`;
}
feedback += `========================================\n`
feedback += `Keterangan\n`
feedback += `========================================\n`
let total = 0;
let harga_nota = 0;
row.detail.forEach((detail, index) => {
total = total + detail.harga;
harga_nota = harga_nota + detail.harga_nota;
feedback += `No Faktur Beli : ${detail.no_faktur_beli}\n`
feedback += `Jenis Barang   : ${detail.kode_dept}\n`
feedback += `Nama Barang    : ${detail.nama_barang}\n`
feedback += `Berat          : ${detail.berat.toFixed(3)}\n`
feedback += `Harga          : ${detail.harga.toLocaleString("kr-KO")}\n`
feedback += `========================================\n`
})
feedback += `Total          : ${total.toLocaleString("kr-KO")}\n`
feedback += `========================================\n`
feedback += `Harga Waktu Beli   : ${total.toLocaleString("kr-KO")}\n`
feedback += `Harga Waktu Jual   : ${harga_nota.toLocaleString("kr-KO")}\n`
feedback += `






`
})

      document.getElementById("myInput").value = feedback;

    }).then(() => {
      console.log('masuk');
      const element = document.createElement("a");
      const file = new Blob([document.getElementById("myInput").value], {
        type: "text/plain;charset=utf-8",
      });
      element.href = URL.createObjectURL(file);
      element.download = "autoprint_pembelian.txt";
      document.body.appendChild(element);
      element.click();
    })
      
      .catch((err) => {
      
    })
  }
  shoBatalPembelian(row) {
    // console.log(row.no_faktur_beli);
    let data = {
      no_faktur_group: row.no_faktur_group,
      };
    //   console.log(data);
    postData("batalpembelian/get/by-faktur", data)
      .then((res) => {
        // console.log(res.data);
        if (res.data.length === 0) {
          ToastNotification("info", res.data.message);
            this.setState({
              FormModal: false,
            });
        } else {
          this.setState({
            FormModal: true,
          });
          res.data.forEach((row, index) => {
            let hasil = {
              no_faktur_group: row._id.no_faktur_group,
              tgl_system: row._id.tgl_system,
              kode_sales: row._id.kode_sales,
              nama_customer: row._id.nama_customer,
              no_hp: row._id.no_hp,
              alamat_customer: row._id.alamat_customer,
              kode_gudang: row._id.kode_gudang,
              detail_barang: row.detail_barang,
            };
            this.props.dispatch(showModalBatalBeli(hasil));
          });
        }
      })
        .catch((err) => {
          console.log(err);
        ToastNotification("info", "Terjadi Kesalahan Saat Request Data");
        this.setState({
          FormModal: false,
        });
      });
  }

  handleSubmit(data) {
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
      ),
    };
    localStorageencryp("tgl_laporan", JSON.stringify(data_post));
    this.setState({
      isLoading: true,
    });
    postData("pembelian/get/by-tanggal", data_post)
      .then((res) => {
        // console.log(res.data);
        if (res.data.length === 0) {
          ToastNotification("info", "Data Pembelian Tidak Ada");
          this.setState({
            isLoading: false,
            datapembelian: [],
          });
        } else {
        ToastNotification("success", "Data Pembelian Tersedia");
          this.setState({
            isLoading: false,
            datapembelian: res.data,
          });
        }
      })
      .catch((err) => {
        //   console.log(err);
        ToastNotification("info", "Data Pembelian Tidak Ada");
        this.setState({
          isLoading: false,
          datapembelian: [],
        });
      });
  }
  batalPembelian(data) {
    // console.log(data);
    this.setState({
      isLoadingBatalBeli: true,
    });
    setTimeout(() => {
      let hasil = {
        no_faktur_group: data.no_faktur_group,
      };
      postData("batalpembelian/simpan", hasil)
        .then((res) => {
          ToastNotification("success", res.data.message);
          this.setState({
            FormModal: false,
            isLoadingBatalBeli: false,
          });
            let tgl_laporan = localStoragedecryp('tgl_laporan').length === 0 ? [] : JSON.parse(localStoragedecryp('tgl_laporan'));
          postData("pembelian/get/by-tanggal", tgl_laporan).then((res) => {
            this.setState({
              datapembelian: res.data,
            });
          });
        })
        .catch((err) => {
          ToastNotification("info", "Data Pembelian Tidak Ada");
          this.setState({
            FormModal: false,
            isLoading: false,
            isLoadingBatalBeli: false,
          });
        });
    }, 500);
  }
  back() {
    this.setState({ FormModal: false });
  }
  
  render() {
    
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Pembelian</Link>
          </li>
          <li className="breadcrumb-item active">Lihat Pembelian</li>
        </ol>
        <h1 className="page-header">Lihat Pembelian </h1>
        {/* <button onClick={()=>this.ReprintPembelian()}> Print </button> */}
        <Panel>
        <textarea
            style={{ display: "none" }}
            id="myInput"
            rows="100"
            cols="100"
          />

          <PanelHeader>
            {" "}
            {this.state.FormModal === true
              ? "Batal Pembelian"
              : "Lihat Pembelian"}
          </PanelHeader>
          <br />
          <PanelBody>
            {this.state.FormModal === true ? (
              <HeadBatalBeli
                back={() => this.back()}
                isLoadingBatalBeli={this.state.isLoadingBatalBeli}
                onSubmit={(data) => this.batalPembelian(data)}
              />
            ) : (
              <>
                {/* Lihat Pembelian */}
                <HeadPembelian
                  onSubmit={(data) => this.handleSubmit(data)}
                  isLoading={this.state.isLoading}
                />
                <Tabel
                  keyField="no_faktur_group"
                  exportCsv={false}
                  data={this.state.datapembelian}
                  columns={this.state.columns}
                  empty={true}
                  textEmpty="Data Barang Kosong"
                />
              </>
            )}
          </PanelBody>
          <br />
          {/* End Tambah Lihat Pembelian  */}
        </Panel>
        {this.state.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </div>
    );
  }
}

export default connect(null)(FormLihatPembelian);
