const ValidasiServicesSelesai = (value) => {
    const errors = {};
    if (!value.tgl_selesai) {
        errors.tgl_selesai = "Tanggal Selesai Tidak Boleh Kosong";
    }
    if (!value.tgl_ambil) {
        errors.tgl_ambil = "Tanggal Tidak Boleh Kosong";
    }
 
    return errors;
}

export default ValidasiServicesSelesai;


