import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { HiidenFiled, ReanderField } from "../../../components/helpers/field";
import Tabel from "../../../components/helpers/tabel";
import ValidasiServicesSelesai from "../../../Validasi/Services/ValidasiServicesSelesai";
import Loading from 'react-fullscreen-loading';

class HeadBatalBeli extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date("Y-m-d"),
      columns: [
        {
          dataField: "nama_barang",
          text: "Nama Barang",
          footer: "",
        },
        {
          dataField: "berat",
          text: "Berat",
          formatter: (cell) => {
            return <span>{parseFloat(cell).toFixed(1)}</span>;
          },
          footer: (columnData) =>
            parseFloat(columnData.reduce((acc, item) => acc + item, 0)).toFixed(
              1
            ),
        },
        {
          dataField: "berat_nota",
          text: "Berat Nota",
          formatter: (cell) => {
            return <span>{parseFloat(cell).toFixed(1)}</span>;
          },
          footer: (columnData) =>
            parseFloat(columnData.reduce((acc, item) => acc + item, 0)).toFixed(
              1
            ),
        },
        {
          dataField: "harga_nota",
          text: "Harga Nota",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "harga",
          text: "Harga",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "harga_rata",
          text: "Harga Rata",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "kondisi",
          text: "Kondisi",
          footer: "",
        },
      ],
    };
  }

  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-4">
            <Field
              id="no_faktur_group"
              name="no_faktur_group"
              component={HiidenFiled}
              type="hidden"
              readOnly={true}
            />
            <Field
              name="nama_customer"
              component={ReanderField}
              type="text"
              readOnly={true}
              label="Nama Customer"
              placeholder="Masukan Nama Customer"
            />
          </div>
          <div className="col-4">
            <Field
              name="no_hp"
              component={ReanderField}
              type="text"
              readOnly={true}
              label="No Hp"
              placeholder="Masukan No Hp"
            />
          </div>
          <div className="col-4">
            <Field
              name="alamat_customer"
              component={ReanderField}
              type="text"
              readOnly={true}
              label="Alamat Customer"
              placeholder="Masukan Alamat Customer"
            />
          </div>
          <div className="col-12">
            <Tabel
              keyField="nama_barang"
              exportCsv={false}
              data={this.props.detail_barang}
              columns={this.state.columns}
              pagination="off"
              empty={true}
              textEmpty="Data Barang Kosong"
            />
          </div>
          <div className="col-3">
            <button
              type="button"
              onClick={this.props.back}
              className="btn btn-primary btn-block"
            >
              <i className="fa fa-arrow-left"></i> Kembali
            </button>
          </div>
          <div className="col-9">
          <button
          tabIndex="2"
              className="btn btn-primary btn-block"
              disabled={this.props.isLoadingBatalBeli}
              type="submit"
            >
              {this.props.isLoadingBatalBeli ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Membatal
                </>
              ) : (
                "Batal Pembelian"
              )}
            </button>
          </div>
        </div>
        <br />
        {this.props.isLoadingBatalBeli ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
        {/* <ModalGlobal title="Pembayaran" content={<ModalPembayaran />} /> */}
      </form>
    );
  }
}

HeadBatalBeli = reduxForm({
  form: "HeadBatalBeli",
  enableReinitialize: true,
  validate: ValidasiServicesSelesai,
})(HeadBatalBeli);
// export default connect(maptostate, null)(HeadBatalBeli);

export default connect((state) => {
  return {
    detail_barang: state.pembelian.showModalBatalBeli.detail_barang,
    initialValues: {
      no_faktur_group: state.pembelian.showModalBatalBeli.no_faktur_group,
      nama_customer: state.pembelian.showModalBatalBeli.nama_customer,
      no_hp: state.pembelian.showModalBatalBeli.no_hp,
      alamat_customer: state.pembelian.showModalBatalBeli.alamat_customer,
    },
  };
})(HeadBatalBeli);
